<template lang="html">
  <div class="wrap">
    <headers></headers>
          <div class="title">
              <span class="titles">消费明细</span>
              /说明说明说明说明说明说明说明说明
              <div class="backs">
              <el-button style="margin-right:14px" @click="dialogTableVisible = true">数据总汇</el-button>
              <el-button style="margin-right:14px">EXCEL 导出</el-button>
              <router-link tag="a" to="/freight">返回上一页 </router-link>
              <i class="el-icon-arrow-right"></i>
              </div>
          </div>
      <div class="container">
           <!--第一部分表单-->
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
              <el-form-item label="关联信息">
                <el-input v-model="formInline.user" placeholder="姓名、车排名、手机号"></el-input>
              </el-form-item>
              <el-form-item label="时间">
                <el-input v-model="formInline.user" placeholder="选择"></el-input>
              </el-form-item>
              
             <el-form-item label="开票状态">
                <el-select v-model="formInline.jd" placeholder="">
                  <el-option label="别无所求" value="one"></el-option>
                  <el-option label="太爱你" value="two"></el-option>
                </el-select>
              </el-form-item>
             <el-form-item label="扣费类型">
                <el-select v-model="formInline.yw" placeholder="可多选位置">
                  <el-option label="上海" value="shanghai"></el-option>
                  <el-option label="北京" value="beijing"></el-option>
                </el-select>
              </el-form-item>
               <el-form-item>
              <el-button type="primary" class="search">查询</el-button>
            </el-form-item>


            </el-form> <!--第一部分表单结束-->
         <!--航线-->  
           <div class="xian"></div>
         <!--第二部分表格-->
            <el-table
                ref="multipleTable"
                :data="tableData3"
                border
                tooltip-effect="dark"
                style="width: 100%"
                >
                <el-table-column
                  type="selection"
                  width="55">
                </el-table-column>
                <el-table-column
                  label="时间"
                >
                  <template scope="scope">{{ scope.row.date }}</template>
                </el-table-column>
                <el-table-column
                  prop="glxx"
                  label="关联信息（运单/手机/人）"
                 >
                </el-table-column>
                <el-table-column
                  prop="kflx"
                  label="扣费类型"
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                  prop="sr"
                  label="收入"
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                  prop="zc"
                  label="支出"
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                  prop="jy"
                  label="结余"
                  show-overflow-tooltip>
                </el-table-column>
              </el-table><!--第二部分表格结束-->
              <div class="caozuo">
              <h4>操作功能:</h4>
              <router-link tag="button" to="/waybill">
              申请开票
              </router-link>
              </div>
             <!--数据总汇弹框--> 
             
              <div class="tankun">
                <el-dialog title="消费明细总汇" :visible.sync="dialogTableVisible">
                  <el-table :data="gridData" border=true>
                    <el-table-column property="demo" label="项目"></el-table-column>
                    <el-table-column property="money" label="金额"></el-table-column>
                    
                  </el-table>
                  <el-table-column class="zonghui">用户总消费:<span>{{zxf()}}</span></el-table-column>
                  <el-button type="primary" class="daochu" @click="dialogTableVisible=false">导出</el-button>
                </el-dialog>
                 
             </div>   
             <!--数据总汇弹框结束--> 
    
      </div><!--.container结束-->    
  </div>
</template>

<script>
import headers from "@/components/header"
export default {
  components:{
    headers
  },
  data(){
    return{
     formInline: {
          jd: '',
          yw: ''
        },
      tableData3: [{
          date: '2016-05-03',
          glxx: '18050987890',
          kflx: '定位',
          sr:'',
          zc:'0.2',
          jy:'0.2'
        }, {
          date: '2016-05-03',
          glxx: '18050987890',
          kflx: '定位',
          sr:'',
          zc:'0.2',
          jy:'0.2'
        }, {
          date: '2016-05-03',
          glxx: '18050987890',
          kflx: '定位',
          sr:'',
          zc:'0.2',
          jy:'0.2'
        }, {
         date: '2016-05-03',
          glxx: '18050987890',
          kflx: '定位',
          sr:'',
          zc:'0.2',
          jy:'0.2'
        }],  
        // 弹框数据
        dialogTableVisible: false,
        gridData: [{
          demo: '司机服务扣费',
          money: 0.00
        }, {
          demo: '司机服务扣费',
          money: 0.00
        }, {
          demo: '司机服务扣费',
          money: 0.00
        }, {
          demo: '司机服务扣费',
          money: 0.00
        }, {
          demo: '司机服务扣费',
          money: 0.00
        }, {
          demo: '司机服务扣费',
          money: 50.00
        }, {
          demo: '司机服务扣费',
          money: 0.00
        }, {
          demo: '司机服务扣费',
          money: 0.00
        }, {
          demo: '司机服务扣费',
          money: 0.00
        }],
    }
    
  },
  methods:{
      // 用户总消费
      zxf(){
        let zj=0;
        for(let i=0;i<this.gridData.length;i++){
          zj+=this.gridData[i].money;
        }
        return zj;
      },
    }
}
</script>

<style lang="less">

.wrap{
   background:#fafbff;
   height:100%;
}
 .title{
    width:1200px;
    margin:0 auto;
    padding:28px 0 24px;
    font-size:12px;
    color:#8291b2;
    border-top:1px solid #edeef2;
     background:#fafbff;
      .titles{
      font-size:16px;
      font-weight:700;
      color:#39509c;
     
    }
     .backs{
     
       float:right;
       .el-button{
         color:green;
         border:1px solid green;
       }
     }
  }
.container{
  border:1px solid #edeef2;
  padding:24px;
  width:1144px;
  
  margin:0 auto;
  background:white;
  .xian{
            background:#e5eaf1; height:2px
        }
  .el-form{
               .el-input{
                  width:200px;
                }
                .search{
                  width:196px;
                  margin-left:320px;
                }
      }
      .caozuo{
        margin-top:14px;
        font-size:10px;
        h4{
          color:#8090ae;
          display:inline-block;
        }
        button{
          margin-left:10px;
          width:70px;height:24px;
          color:#fff;
          background:#64cc9d;
          border-radius:3px;
          border:none;
        }
      }
      .tankun{
        .el-table__row{
          .cell{
            text-align:center;
          }
        }
        .zonghui{
          text-align:center;
          border:1px solid #e5eaf1;
          height:30px;
          line-height:30px;
          span{
            color:red;
            
          }
        }
        .daochu{
          float:right;
          margin:16px;
          background:green;
        }
      }
 
}


</style>


